import React from 'react'

export interface RecipeListItemData {
  id: number;
  name: string;
  link: string;
}

interface RecipeListData {
  recipes: Array<RecipeListItemData>
}

const RecipeListSimple = (props: RecipeListData) => {
  return (
    <div>
      <ol>
        {props.recipes.map(recipe =>
          <li key={recipe.id}>
            <a href={recipe.link} target="_blank" rel="noreferrer">{recipe.name}</a>
          </li>
        )}
      </ol>
    </div>
  )
}

export default RecipeListSimple