import * as React from 'react';
import Autocomplete, { AutocompleteChangeReason } from '@mui/material/Autocomplete';
import TextField from '@mui/material/TextField';

export interface Tag {
  label: string
}

interface TagsInterface {
  label: string,
  options: Tag[]
  value: Tag[]
  setValue: (newTags: Tag[]) => void
}

export default function Tags(props: TagsInterface) {
  return (
    <Autocomplete
      multiple
      value={props.value} // TODO: Change rendering if value is not in options.
      onChange={(event: any, newValue: Tag[], reason: AutocompleteChangeReason) => {
        // console.log(newValue)
        props.setValue(newValue);
      }}
      id="tags-outlined"
      options={props.options}
      getOptionLabel={(option) => option.label}
      // defaultValue={props.value} // opt to use controlled Autocomplete?
      filterSelectedOptions
      freeSolo={false} // TODO maybe make this possible?
      isOptionEqualToValue={(option, value) => 
        option.label === value.label
      }
      renderInput={(params) => (
        <TextField
          {...params}
          label={props.label}
        />
      )}
      autoComplete={true}
      autoHighlight={true}
      autoSelect={true}
    />
  );
}