// var Papa = require("papaparse.js");
import Papa, { ParseResult } from 'papaparse';
import * as constants from '../store/Constants';
import { Recipe } from '../store/Recipe';
import { setLocalStorage } from './LocalStorageHelpers'

export type GetRecipeIndexInterface = {
  url: string,
  setData: (data: Recipe[]) => void
}

export const getRecipeIndex = (props: GetRecipeIndexInterface) => {
  console.log("Download triggered...")
  // Papa parse does a XMLHttpRequest
  // And returns a "parse result" object
  // We should pull out the data in our desired format
  // and return it to Main as state. :)

  Papa.parse(props.url, {
    download: true,
    header: true, // uses similar format to pandas to_json records, each index will be a json object
    dynamicTyping: true, // turn on for time
    complete: function (results: ParseResult<Record<string, unknown>>) {
      console.log("Parsing complete:", results);
      // TODO: Add check for errors? Maybe do something with meta.fields?
      const dataArray = results.data.map( (record) => 
        {
          return {
              [constants.Name]: record[constants.Name],
              [constants.Category]: record[constants.Category],
              [constants.Cuisine]: record[constants.Cuisine],
              [constants.Form]: record[constants.Form],
              [constants.Time]: record[constants.Time],
              [constants.Method]: record[constants.Method],
              [constants.Vegetarian]: record[constants.Vegetarian],
              [constants.Ingredients]: record[constants.Ingredients],
              [constants.Notes]: record[constants.Notes],
              [constants.Link]: record[constants.Link]
          } as Recipe
        }
      )

      setLocalStorage({index: dataArray, url: props.url, date: new Date()})
      props.setData(dataArray)
    }
  })
}

export {}