import React, { useState } from 'react'
import { useSearchParams } from "react-router-dom";
import * as constants from "../../store/Constants";
import RecipeListSimple from '../../components/recipes/RecipeListSimple';
import Tags, { Tag } from '../../components/search/Tags';
import { getRecipeIndex } from '../../utils/ServerCommunications';
import { Recipe } from '../../store/Recipe';
import AdvancedSearch from '../../components/search/AdvancedSearch';
import { getAllOptions, updateQuery } from '../../utils/QueryHelpers';
import { filterIndex } from '../../utils/Filter';
import Container from '@mui/material/Container';
import Collapse from '@mui/material/Collapse';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import Baguette from '../../components/baguette/Baguette';
import styles from '../../components/baguette/baguette.module.css';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import { getLocalStorage } from '../../utils/LocalStorageHelpers'

function Main() {
  // State will consist of the search parameters and the recipe index.
  let [searchParams, setSearchParams] = useSearchParams();
  
  // Use localStorage available if possible.
  const localStorageResult = getLocalStorage();
  const [recipeIndex, setRecipeIndex] = useState<Recipe[]>(localStorageResult.index);

  // The filteredIndex is the result of the combination of the above.
  const filteredIndex = filterIndex({query: searchParams, index: recipeIndex});

  // Temp starting data. Will eventually pull by default from a different location.
  const url = "https://docs.google.com/spreadsheets/d/e/2PACX-1vTEHXP26HVamXzVDy3ysNUSdfG9etdIcQ1_3XHP58LXOxMswccxX9HKf85QcgLgUzcXP_cDpTFH6oY9/pub?gid=0&single=true&output=csv"
  // All possible tags from index (but from a specific set of columns).
  const tags = getAllOptions({index: filteredIndex}).map( option => { return { label: option } })

  // Parse URL for keyword query.
  const currentKeywords = searchParams.getAll(constants.Keyword).map((name) =>
      {
        return {
          label: name
        } as Tag
      }
    )

  // Helper function for setting URL with new keyword query.
  function setKeywordQuery(tags: Tag[]) {
    updateQuery({
      column: constants.Keyword,
      query: searchParams,
      tags: tags,
      setQuery: setSearchParams
    })
  }

  // Handle show/hide of advanced search.
  const [showAdvancedSearch, setShowAdvancedSearch] = React.useState(false);
  const toggleAdvancedSearch = () => {
    setShowAdvancedSearch((prev) => !prev);
  };

  // Check if 
  var lastSync = <p>Last Sync On: ?</p>
  if (localStorageResult.date !== undefined) {
    lastSync = <p>Last Sync On: {localStorageResult.date.toLocaleString()}</p>
  }

  return (
    <Container>
      <Baguette/>
      <div className={styles.underoval}>
        <Button 
          variant="contained" 
          onClick={() => getRecipeIndex({url: url, setData: (index: Recipe[]) => { setRecipeIndex(index) }})}>
            Sync
        </Button>
        {lastSync}
        <div>
          <h3>Search the MAK Protocols</h3>
          <Tags
            label={"Keyword"}
            options={tags}
            value={currentKeywords}
            setValue={setKeywordQuery}
          />
        </div>
        <div>
          <FormControlLabel
            control={<Switch checked={showAdvancedSearch} onChange={toggleAdvancedSearch} />}
            label="Show Advanced Search"
          />
          <Collapse in={showAdvancedSearch}>
            {/* <h3>Advanced Search</h3> */}
            <AdvancedSearch
              index={filteredIndex}
              query={searchParams}
              setQuery={setSearchParams}
            />
          </Collapse>
        </div>
        <Box sx={{padding: '10px'}}>
          <p> Showing {filteredIndex.length}/{recipeIndex.length} recipes. </p>
          <RecipeListSimple 
            recipes={filteredIndex.map( (recipe, index) =>
              {
                return {
                  id: index,
                  name: recipe['Name'],
                  link: recipe['Link']
                }
              }
            )}
          />
        </Box>
      </div>
    </Container>
  )
}

export default Main