import { Recipe } from "../store/Recipe"
import * as constants from '../store/Constants'

interface runKeywordFilterInterface {
  keyword: string,
  index: Recipe[]
}

const runKeywordFilter = (props: runKeywordFilterInterface) => {
  return props.index.filter( recipe => {
    return constants.KeywordQueryOptions.some( attribute => {
      return recipe[ attribute ].toLowerCase().includes( props.keyword.toLowerCase() );
    })
  })
}

interface runKeywordFilterRecursiveInterface {
  keywords: string[],
  index: Recipe[],
  counter: number
}

const runKeywordFilterRecursive = (props: runKeywordFilterRecursiveInterface): Recipe[] => {
  if (props.counter === props.keywords.length) {
    return runKeywordFilter(
      {
        keyword: props.keywords[props.counter - 1],
        index: props.index
      }
    )
  }
  return runKeywordFilterRecursive(
    {
      keywords: props.keywords,
      index: runKeywordFilter(
        {
          keyword: props.keywords[props.counter],
          index: props.index
        }
      ),
      counter: props.counter + 1
    }
  )
}

interface runColumnFilterInterface {
  column: string,
  tag: string,
  index: Recipe[]
}

const runColumnFilter = (props: runColumnFilterInterface) => {
  return props.index.filter( recipe => {
    return recipe[ props.column ].toLowerCase().includes( props.tag.toLowerCase() );
  })
}

interface runColumnFilterRecursiveInterface {
  column: string,
  tags: string[],
  index: Recipe[],
  counter: number
}

const runColumnFilterRecursive = (props: runColumnFilterRecursiveInterface): Recipe[] => {
  if (props.counter === props.tags.length) {
    return runColumnFilter(
      {
        column: props.column,
        tag: props.tags[props.counter - 1],
        index: props.index
      })
  }
  return runColumnFilterRecursive(
    {
      column: props.column,
      tags: props.tags,
      index: runColumnFilter(
        {
          column: props.column,
          tag: props.tags[props.counter],
          index: props.index
        }),
      counter: props.counter + 1
    }
  )
}

interface filterByTimeInterface {
  minTime: number,
  maxTime: number,
  index: Recipe[]
}

const filterByTime = (props: filterByTimeInterface) => {
  return props.index.filter( recipe => {
    const time = +(recipe[ constants.Time ])
    return time >= props.minTime && time <= props.maxTime
  })
}

interface filterByVegetarianInterface {
  vegetarianState: string,
  index: Recipe[]
}

const filterByVegetarian = (props: filterByVegetarianInterface) => {
  if (props.vegetarianState === "all") {
    return props.index
  } else if (props.vegetarianState === "yes") {
    return props.index.filter( recipe => {
      return recipe["Vegetarian"].toLowerCase() === "y" || recipe["Vegetarian"].toLowerCase() === "m"
    })
  } else {
    return props.index.filter( recipe => {
      return recipe["Vegetarian"].toLowerCase() === "n"
    })
  }
}

interface filterIndexInterface {
  query: URLSearchParams,
  index: Recipe[]
}

export const filterIndex = (props: filterIndexInterface) => {
  // Do keyword search.
  var keywordFilterResult = props.index
  const keywords = props.query.getAll(constants.Keyword)
  if (keywords.length > 0) {
    keywordFilterResult = runKeywordFilterRecursive(
      {
        keywords: keywords,
        index: props.index,
        counter: 0
      }
    )
  }

  // Do advanced search.
  var categoryFilterResult = keywordFilterResult
  var cuisineFilterResult = categoryFilterResult
  var formFilterResult = cuisineFilterResult
  var timeFilterResult = formFilterResult
  var methodFilterResult = timeFilterResult
  var vegetarianFilterResult = methodFilterResult
  var ingredientFilterResult = vegetarianFilterResult

  const categories = props.query.getAll(constants.Category)
  if (categories.length > 0) {
    categoryFilterResult = runColumnFilterRecursive({column: constants.Category, tags: categories, index: keywordFilterResult, counter: 0})
  } else {
    categoryFilterResult = keywordFilterResult
  }

  const cuisines = props.query.getAll(constants.Cuisine)
  if (cuisines.length > 0) {
    cuisineFilterResult = runColumnFilterRecursive({column: constants.Cuisine, tags: cuisines, index: categoryFilterResult, counter: 0})
  } else {
    cuisineFilterResult = categoryFilterResult
  }

  const forms = props.query.getAll(constants.Form)
  if (forms.length > 0) {
    formFilterResult = runColumnFilterRecursive({column: constants.Form, tags: forms, index: cuisineFilterResult, counter: 0})
  } else {
    formFilterResult = cuisineFilterResult
  }

  // Get time query
  const minTimeQuery = props.query.get(constants.MinTime)
  const maxTimeQuery = props.query.get(constants.MaxTime)
  var minTimeState = 0
  var maxTimeState = 24
  if (minTimeQuery !== null && maxTimeQuery !== null) {
    minTimeState = +minTimeQuery
    maxTimeState = +maxTimeQuery
  }
  // Filter by time query.
  timeFilterResult = filterByTime(
    {
      minTime: minTimeState,
      maxTime: maxTimeState,
      index: formFilterResult
    }
  )

  const methods = props.query.getAll(constants.Method)
  if (methods.length > 0) {
    methodFilterResult = runColumnFilterRecursive({column: constants.Method, tags: methods, index: timeFilterResult, counter: 0})
  } else {
    methodFilterResult = timeFilterResult
  }

  // Get vegetarian query.
  const vegetarianQuery = props.query.get(constants.Vegetarian)
  var vegetarianQueryState = "all"
  if (vegetarianQuery !== null) {
    vegetarianQueryState = vegetarianQuery
  }
  vegetarianFilterResult = filterByVegetarian({vegetarianState: vegetarianQueryState, index: methodFilterResult})

  const ingredients = props.query.getAll(constants.Ingredients)
  if (ingredients.length > 0) {
    ingredientFilterResult = runColumnFilterRecursive({column: constants.Ingredients, tags: ingredients, index: vegetarianFilterResult, counter: 0})
  } else {
    ingredientFilterResult = vegetarianFilterResult
  }

  return ingredientFilterResult

}