// This file will hopefully help build the correct URL.

import { Recipe } from "../store/Recipe"
import Tags, { Tag } from '../components/search/Tags'
import { URLSearchParamsInit } from 'react-router-dom'
import * as constants from '../store/Constants'

interface getOptionsInterface {
  column: string,
  index: Recipe[]
}

export const getOptions = (props: getOptionsInterface) => {
  return (
    [...new Set( //set removes the duplicates
      props.index.flatMap(recipe => recipe[props.column]
        .split(',')) // some attributes have multiple, split will break them and flat in flatmap will flatten them out
        .map(recipe => recipe.trim()) // trim off excess whitespace (also important for the Set)
    )]
      .sort() // sort alphebetically
      .filter(recipe => recipe) // remove all the empty strings
  )
}

interface getAllOptionsInterface {
  index: Recipe[]
}

export const getAllOptions = (props: getAllOptionsInterface) => {
  const allOptions = [...new Set(constants.KeywordQueryOptions.flatMap(attribute =>
    getOptions({
      column: attribute,
      index: props.index
    }
    ))
  )]
  // We also need to split up each multi-word attribute (ex: "olive oil" becomes "olive" "oil").
  const allOptionsWithNameSplit = [...new Set(allOptions
    .flatMap( attribute => attribute.toLowerCase().split(' '))
    .sort()
  )]
  return allOptionsWithNameSplit
}

interface getCurrentQueryInterface {
  column: string,
  query: URLSearchParams
}

export const getCurrentQuery = (props: getCurrentQueryInterface) => {
  return props.query.getAll(props.column).map((name) =>
    {
      return {
        label: name
      } as Tag
    }
  )
}

interface updateQueryInterface {
  column: string,
  query: URLSearchParams,
  tags: Tag[],
  setQuery: (newParams: URLSearchParamsInit) => void
}

// This will return a new set of search params.
export const updateQuery = (props: updateQueryInterface) => {
    // We want to preserve the other pieces of the query.
    // Let's first remove the previous query.
    props.query.delete(props.column)
    // And then add it back, but with the new content.
    for (let tag of props.tags) {
      props.query.append(props.column, tag.label)
    }
    // Now actually set the query.
    props.setQuery(props.query)
}